<template>
  <div>
    <!--面包屑导航-->
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <a href="">首页</a>&gt;<a href="">产品中心</a>&gt;<a href="">净美仕净化器</a>
        </h2>
      </div>
    </div>
    <!--页面主体-->
    <div class="main container">
      <div class="pd_info clearfloat">
        <div class="pdinfo_img">
          <img :src="productPic" alt=""/>
        </div>
        <div class="pdinfo_text">
          <h2>{{product.title1}}</h2>
          <ul>
            <li>型号：{{product.model}}</li>
            <li>功能：{{product.func}}</li>
            <li>噪音：{{product.noise}}</li>
            <li>风量：{{product.wind}}</li>
            <li>适用对象：{{product.applyTo}}</li>
            <li>适用面积：{{product.size}}</li>
            <li>空气净化能效等级：{{product.level}}</li>
            <!--<li>型号：M8088A</li>-->
            <!--<li>功能：定时、除甲醛、杀菌</li>-->
            <!--<li>噪音：19-60dB</li>-->
            <!--<li>风量：690m³/小时</li>-->
            <!--<li>适用对象：商用、家用</li>-->
            <!--<li>适用面积：41㎡-60㎡</li>-->
            <!--<li>空气净化能效等级：合格级</li>-->
          </ul>
          <p>价格：<span>¥</span><strong id="price">{{product.price}}</strong></p>
          <a @click.prevent="addCart(product.pid)" id="addCart"><span class="icon_cart"></span>加入购物车</a>
        </div>
      </div>
      <div class="details_box">
        <h2 class="title">产品详情</h2>
        <div class="pd_details" v-html="product.detail">
          <!--<p>家用除甲醛静音M-8088A，带大脑的空气净化器。家用除甲醛静音M-8088A，带大脑的空气净化器。家用除甲醛静音M-8088A，带大脑的空气净化器。家用除甲醛静音M-8088A，带大脑的空气净化器。家用除甲醛静音M-8088A，带大脑的空气净化器。家用除甲醛静音M-8088A，带大脑的空气净化器。家用除甲醛静音M-8088A，带大脑的空气净化器。</p>-->
          <!--<img src="product-imgs/m8088a/01.jpg" alt=""/>-->
          <!--<img src="product-imgs/m8088a/02.jpg" alt=""/>-->
          <!--<img src="product-imgs/m8088a/03.jpg" alt=""/>-->
          <!--<img src="product-imgs/m8088a/04.jpg" alt=""/>-->
          <!--<img src="product-imgs/m8088a/05.jpg" alt=""/>-->
          <!--<img src="product-imgs/m8088a/06.jpg" alt=""/>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      pid:0,
      product:{},
      productPic:''
    }
  },
  mounted(){
    this.pid=this.$route.params.pid;
    let url='http://127.0.0.1:8080/product/detail/'+this.pid;
    this.$http.get(url).then(result=>{
      this.product=result.body;
      this.productPic=require('../assets/'+this.product.pic);
    })
  },
  methods:{
    addCart(pid){
      let uid=sessionStorage.uid;
      let uname=sessionStorage.uname;
      if(!uid||!uname){//判断是否登录，如果没有登录就跳转到登录界面
        this.$router.push('/login');
      }else{//如果登录了就ajax请求加入购物车
        let url='http://127.0.0.1:8080/cart/detail/add';
        this.$http.post(url,{uid,pid}).then(result=>{
          if(result.body.code==1){
            alert('添加成功!');
            this.bus.$emit('getHeaderCartDetails');
          }
        });
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



